import { v4 as uuidv4 } from 'uuid';
import React from 'react';
import { Form, Label, Input, Button } from './PhoneBook.styled';

class Phonebook extends React.Component {
  state = {
    name: '',
    number: '',
  };

  handleSubmitForm = evt => {
    evt.preventDefault();
    const newContactName = this.state.name;
    const currentContacts = this.props.contacts;

    const normalizedName = currentContacts.find(
      ({ name }) => name.toLowerCase() === newContactName.toLowerCase(),
    );

    if (normalizedName) {
      alert(`${newContactName} is alredy in contacts.`);
      this.reset();
      return;
    }

    this.props.onSubmit(this.state);
    this.reset();
  };

  handleInputChange = evt => {
    this.setState({
      [evt.currentTarget.name]: evt.currentTarget.value,
    });
  };

  reset = () => {
    this.setState({
      name: '',
      number: '',
    });
  };

  render() {
    const { name, number } = this.state;
    const formNameId = uuidv4();
    const formNumberId = uuidv4();

    return (
      <Form onSubmit={this.handleSubmitForm}>
        <Label htmlFor={formNameId}>
          Name
          <Input
            id={formNameId}
            type="text"
            value={name}
            name="name"
            pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
            title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
            required
            onChange={this.handleInputChange}
          />
        </Label>

        <Label htmlFor={formNumberId}>
          Number
          <Input
            id={formNumberId}
            type="tel"
            name="number"
            value={number.trim()}
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
            required
            onChange={this.handleInputChange}
          />
        </Label>
        <Button type="submit" onClick={this.addNameFhoneBook}>
          Add contact
        </Button>
      </Form>
    );
  }
}

export default Phonebook;
